import React from "react";
import { myFirestore } from "../../Config/Firebase";
import "../ContactUs/ContactUs.css";

class ContactUs extends React.Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      message: "",
      isOpen: false,
      isSuccessOpen: false,
      isUnsuccessOpen: false,
    };
    this.text = "";

    this.handleChange = this.handleChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleChange = (event) => {
    event.preventDefault();
    const { name, value } = event.target;

    this.setState({ [name]: value });
  };

  onSubmit() {
    let user = {
      name: this.state.name,
      email: this.state.email,
      message: this.state.message,
    };
    if (user.name === "" || user.email === "" || user.message === "") {
      this.setState({ isOpen: true });
    } else {
      let username = user.email.split("@")[0];
      myFirestore
        .collection("contact")
        .doc("message - " + username)
        .set(user);
      this.setState(
        {
          name: "",
          email: "",
          message: "",
        },
        function (error) {
          if (error) {
            this.setState({ isUnsuccessOpen: true });
          } else {
            this.setState({ isSuccessOpen: true });
          }
        }
      );
    }
  }

  handleClose = () => {
    this.setState({ isOpen: false });
    this.setState({ isSuccessOpen: false });
    this.setState({ isUnsuccessOpen: false });
  };

  render() {
    return (
      <div className="container-fluid contact" id="contact">
        <div className="row" data-aos="fade-up" data-aos-duration="1000">
          <div className="col-1"></div>
          <div className="col-3 head-text">Contact Us</div>
          <div className="col-8"></div>

          <div className="col-1"></div>
          <div className="col-10">
            <div
              className="row contact-card"
              data-aos="zoom-in"
              data-aos-duration="1000"
              data-aos-delay="300"
            >
              <div
                className="col-6"
                data-aos="fade-right"
                data-aos-duration="1000"
                data-aos-delay="400"
              >
                <div className="row">
                  <div className="col-12 img-div"></div>

                  <div className="col-12 blue-bg">
                    <div className="row">
                      <div className="col-12 section-divider"></div>

                      <div className="col-12 section-divider"></div>

                      <div className="col-2 icon-div">
                        <img
                          src={require("../../Assets/location.svg")}
                          height="30px"
                          alt="Map"
                        />
                      </div>
                      <div className="col-10 contact-text">
                        +Richardson, Dallas TX
                      </div>

                      <div className="col-12 section-divider"></div>

                      <div className="col-2 icon-div">
                        <img
                          src={require("../../Assets/mobile.svg")}
                          height="30px"
                          alt=""
                        />
                      </div>
                      <div className="col-10 contact-text">+14693509711</div>

                      <div className="col-12 section-divider"></div>

                      <div className="col-2 icon-div">
                        <img
                          src={require("../../Assets/mail.svg")}
                          height="23px"
                          alt=""
                        />
                      </div>
                      <div className="col-10 contact-text">
                        team@reallos.com
                      </div>

                      <div className="col-12 section-divider"></div>

                      <div className="col-12 section-divider"></div>

                      <div className="col-4 contact-subtext">
                        Connect with us:
                      </div>
                      <div className="col-1">
                        <img
                          src={require("../../Assets/Group 213.svg")}
                          height="30px"
                          alt=""
                        />
                      </div>
                      <div className="col-1">
                        <img
                          src={require("../../Assets/Group 15.svg")}
                          height="30px"
                          alt=""
                        />
                      </div>
                      <div className="col-1">
                        <img
                          src={require("../../Assets/Group 214.svg")}
                          height="30px"
                          alt=""
                        />
                      </div>
                      <div className="col-12 section-divider"></div>
                      <div className="col-12 section-divider"></div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                className="col-6"
                data-aos="fade-left"
                data-aos-duration="1000"
                data-aos-delay="400"
              >
                <div className="contact-subhead">Leave us a Message</div>
                <form>
                  <div class="form-group">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Name"
                      name="name"
                      onChange={this.handleChange}
                      onBlur={this.handleChange}
                      value={this.state.phone}
                    ></input>
                    <input
                      type="email"
                      className="form-control"
                      placeholder="Email"
                      name="email"
                      onChange={this.handleChange}
                      onBlur={this.handleChange}
                      value={this.state.phone}
                    ></input>
                    <textarea
                      class="form-control"
                      placeholder="Message"
                      rows="4"
                      name="message"
                      onChange={this.handleChange}
                      onBlur={this.handleChange}
                      value={this.state.phone}
                    ></textarea>
                    <button
                      type="button"
                      className="btn btn-primary float-right"
                    >
                      Send
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="col-1"></div>
        </div>
      </div>
    );
  }
}

export default ContactUs;
