import React from "react";
import "../Footer/Footer.css";

function Footer() {
  return (
    <div class="container-fluid footer">
      <div class="row justify-content-center">
        <div class="col-auto">
          <div
            data-aos="flip-down"
            data-aos-duration="1000"
          >
            <img
              src={require("../../Assets/reallos-logo-white.svg")}
              alt="Reallos-Logo"
              height="50px"
              className="mx-auto d-block"
            />
            <div
              class="footer-text"
            >
              Copyright 2020 &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;
              <span class="footer-text-bold"> Reallos Technology LLC </span>
              &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp; All Rights Reserved
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
