import React from "react";
import "../Navbar/Navbar.css";
import { Link } from "react-scroll";
function Navbar() {
  return (
    <div className="navbar navbar-custom">
      <div className="container-fluid">
        <img
          src={require("../../Assets/reallos-logo-white.svg")}
          alt="Reallos-Logo"
          height="50px"
        />
        <ul className="nav justify-content-end">
          <li className="nav-item">
            <button className="btn nav-link">
              <Link
                activeClass="active"
                to="app"
                spy={true}
                smooth={true}
                offset={25}
                duration={500}
              >
                App
              </Link>
            </button>
          </li>
          <li className="nav-item">
            <button className="btn nav-link">
              <Link
                activeClass="active"
                to="testimonial"
                spy={true}
                smooth={true}
                offset={-30}
                duration={500}
              >
                Testimonials
              </Link>
            </button>
          </li>
          <li className="nav-item">
            <button className="btn nav-link">
              <Link
                activeClass="active"
                to="contact"
                spy={true}
                smooth={true}
                offset={20}
                duration={500}
              >
                Contact
              </Link>
            </button>
          </li>
          <button className="btn nav-btn">
            <img src={require("../../Assets/reallos-logo-r.svg")} alt="R" height="40px" />
            For Business
          </button>
        </ul>
      </div>
    </div>
  );
}
export default Navbar;
