import React from "react";
import "./Testimonial.css";

function Testimonial() {
  return (
    <div className="container-fluid testimonial" id="testimonial">
      <div className="row">
        <div
          className="col-12 testimonial-header"
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          Testimonials
        </div>

        <div className="col-12"
          data-aos="zoom-in"
          data-aos-duration="1000"
          data-aos-delay="300"
        >
          <div
            className="carousel slide"
            data-ride="carousel"
            data-interval="false"
            id="quote-carousel"
          >
            <div className="carousel-inner text-center">
              <div className="carousel-item">
                <div className="row justify-content-center">
                  <div className="col-8 col-sm-8">
                    <p className="quote">
                      <img
                        src={require("../../Assets/double-quotes-start.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                      sed do eiusmod tempor incididunt ut labore et dolore magna
                      aliqua.
                      <img
                        src={require("../../Assets/double-quotes-end.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                    </p>
                  </div>
                </div>
              </div>

              <div className="carousel-item active">
                <div className="row justify-content-center">
                  <div className="col-8 col-sm-8">
                    <p className="quote">
                      <img
                        src={require("../../Assets/double-quotes-start.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                      sed do eiusmod tempor incididunt ut labore et dolore magna
                      aliqua.
                      <img
                        src={require("../../Assets/double-quotes-end.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                    </p>
                  </div>
                </div>
              </div>

              <div className="carousel-item">
                <div className="row justify-content-center">
                  <div className="col-8 col-sm-8">
                    <p className="quote">
                      <img
                        src={require("../../Assets/double-quotes-start.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                      sed do eiusmod tempor incididunt ut labore et dolore magna
                      aliqua.
                      <img
                        src={require("../../Assets/double-quotes-end.svg")}
                        alt=""
                        height="60%"
                        className="double-quotes"
                      />
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <ol className="carousel-indicators">
              <li data-target="#quote-carousel" data-slide-to="0">
                <img
                  src={require("../../Assets/Test-img.svg")}
                  alt=""
                  width="100%"
                />
              </li>
              <li
                data-target="#quote-carousel"
                data-slide-to="1"
                className="active"
              >
                <img
                  src={require("../../Assets/Test-img.svg")}
                  alt=""
                  width="100%"
                />
              </li>
              <li data-target="#quote-carousel" data-slide-to="2">
                <img
                  src={require("../../Assets/Test-img.svg")}
                  alt=""
                  width="100%"
                />
              </li>
            </ol>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Testimonial