import React from "react";
import "./Home.css";

function Home() {
  return (
    <div className="container-fluid Home">
      <div className="row align-items-center Home">
        <div
          className="col-5 Home-div"
          data-aos="flip-right"
          data-aos-duration="1000"
        >
          <div
            className="Home-text"
            data-aos="fade-right"
            data-aos-duration="1000"
            data-aos-delay="300"
          >
            Real Estate, Real Easy!
            <br />
            Signup now for our close Beta
          </div><div
            data-aos="fade-right"
            data-aos-duration="1000"
            data-aos-delay="400"
          >
          <button
            type="button"
            className="btn Home-signup-button"
          >
            Signup
          </button></div>
        </div>
      </div>
    </div>
  );
}

export default Home;
