import React from "react";
import { myFirestore } from "../../Config/Firebase";
import "./Notified.css";

class Notified extends React.Component {
  constructor() {
    super();
    this.state = {
      email: "",
      phone: "",
    };

    this.handleChange = this.handleChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  handleChange = (event) => {
    event.preventDefault();
    const { name, value } = event.target;

    this.setState({ [name]: value });
  };

  onSubmit() {
    let user = {
      email: this.state.email,
      phone: this.state.phone,
    };
    if (user.email === "" || user.phone === "") {
      //  Please fill all the fields
    } else {
      let username = user.email.split("@")[0];
      myFirestore
        .collection("partner")
        .doc("user - " + username)
        .set(user);
      this.setState(
        {
          email: "",
          phone: "",
        },
        function (error) {
          if (error) {
            // Data did not saved successfully
          } else {
            // Data saved successfully
          }
        }
      );
    }
  }

  render() {
    return (
      <div className="notified">
        <div className="container-fluid">
          <div className="row align-items-end justify-content-center">
            <div
              className="col-12 notified-header"
              data-aos="fade-up"
              data-aos-duration="1000"
            >
              Get notified when we are live
            </div>

            <div
              className="col-12" style={{height:"30px"}}
            >
            </div>

            <div
              className="col-4 notified-form-div"
              data-aos="fade-right"
              data-aos-duration="1000"
              data-aos-delay="300"
            >
              <form>
                <div class="form-group notified-form">
                  <img
                    src={require("../../Assets/notified-form-img.png")}
                    alt="Notified-Img"
                    className="img-fluid"
                    width="45%"
                  />
                  <input
                    type="text"
                    className="form-control notified-form-field"
                    placeholder="your@example.com"
                    name="email"
                    onChange={this.handleChange}
                    onBlur={this.handleChange}
                    value={this.state.email}
                  />
                  <input
                    type="text"
                    className="form-control notified-form-field"
                    placeholder="+1 9898666666"
                    name="phone"
                    onChange={this.handleChange}
                    onBlur={this.handleChange}
                    value={this.state.phone}
                  />
                  <button type="button" className="btn notified-form-btn">
                    Get Notified
                  </button>
                </div>
              </form>
            </div>
            <div
              className="col-7 d-flex notified-rt-div text-center"
              data-aos="fade-left"
              data-aos-duration="1000"
              data-aos-delay="300"
            >
              <div className="d-flex align-items-center notified-text">
              Text goes here, Text goes here, Text goes here, Text goes here, Text goes here</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Notified;
